.main-nav {
  
  background-color: #1c1531;
  opacity: 50%;
  display: flex;

  &__list {
    padding-left: 0;
    width: 100%;
    display: flex;
    justify-content: space-around;
  }


  &__item {
    display: inline-block;
    // justify-content: flex-end;
  }


  &__link {
    text-decoration: none;
    font-family: $font-p;
    font-style: 16px;
    line-height: 36px;
    color: #fff;
    margin: 24px 0;
  }

}