.slider {
  width: 45%;
  // overflow: hidden;
  display: block;

  &-wrapper{
    // width: 200px;
  }

  &__item {
    color: #201c38;
    font-family: $font-h;
    font-size: 16px;
    line-height: 30px;
    display: block;
    width: 100px;
  }

  &__item__left{
    float: left;
    width: 50%;
  }

  &__item__right{
    width: 50%;
    float: right;
    background-image: url("../../img/message.png");
    background-position: -10px -10px;
  background-repeat: no-repeat;
    display: inline-block;
    vertical-align: top;
  }
}
