.slide-section {

  &__row {
        display: inline-block;
        position: relative;
        
  }

  &__info {

  }

  &__title {
    color: #201c38;
    font-family: $font-p;
    font-size: 30px;
    line-height: 36px;
  }

    &__visual{
    display: inline-block;
    float: right;
    width: 50%;
    position: absolute;
    top: 0;
    right: 0;
  }

  &__img{
    display: inline-block;
    float: right;
  }

  &__photo{
    width: 570px;
    height: 521px;
    display: inline-block;
  }
}