.steps{
  width: 100%;
  display: inline-block;
  vertical-align: top;
}

.step{
  display: inline-block;
  width: 33%;
  margin-top: 0;
  vertical-align: top;
  margin-right: 0;
  margin-left: 0;
  padding-left: 0;
  padding-right: 0;
  position: relative;

  &-number{
    display: inline-block;
    color: #e6e6e6;
    font-size: 45px;
    font-weight: bold;
    font-family: $font-h;
    margin-bottom: 0;
  }

  &-text{
    color: #201c38;
    font-family: $font-h;
    font-size: 16px;
    line-height: 30px;
    vertical-align: top;
    position: relative;
    margin-bottom: 60px;
  }
}

.step:before{
    content:'';
    width: 15px;
    height: 15px;
    background-color: #03eaa5;
    display: inline-block;
    position: absolute;
    top: 82px;
}

.step:after{
    content:'';
    width: 101%;
    height: 2px;
    background-color: #03eaa5;
    display: inline-block;
    position: absolute;
    top: 90px;
}

